import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export class DoctorRegister extends Component {
  render() {
    return (
        <div data-aos="fade-up" data-aos-delay="200" className='auth-background'>
        <br /><br />
        <div className='' >
                <div className='jumbotron col-lg-4 offset-lg-4 form-box'>
                        <h3 className='text-center form-title'>  </h3>
                        
                        <form dir='rtl'>
                                <div className="form-group">
                                        
                                        <label htmlFor="fullName"> </label>
                                        <input type="text" className="form-control" placeholder="  " name='fullName' required />
                                </div>
                                <div className="form-group">
                                        <label htmlFor="email"> </label>
                                        <input type="email" className="form-control" name='email' placeholder="  " required />
                                        <small id="emailHelp" className="form-text text-muted"></small>
                                </div>
                                <div className="form-group">
                                        <label htmlFor="mobileNumber"> </label>
                                        <input type="text" className="form-control" placeholder="  " name='mobileNumber' required />
                                </div>
                              
                                <div className="form-group">
                                        <label htmlFor="password"> </label>
                                        <input type="password" className="form-control" name='password' placeholder="  " required />
                                </div>
                                <div className="form-group">
                                        <label htmlFor="passwordConfirmation">  </label>
                                        <input type="password" className="form-control" name='passwordConfirmation' placeholder="   " required />
                                </div>
                                
                                <button className='btn btn-primary'>  <Link to='' className='links-buttons'  ></Link> </button>
                                <br />
                             <div className='pb-3'>     <Link to='/login/doctor' className='links-buttons'> </Link></div>
                        </form>
                </div>
        </div>
</div>
    )
  }
}

export default DoctorRegister
